/*----------------------------------*\
  	Variables - Breakpoint Sizes
\*----------------------------------*/
/*
	Media Query Breakpoint Sizes
*/
/* Nano */
$small-mobile-screen-from: 0px !default;
$small-mobile-screen-to: 320px !default;

/* Micro */
$mobile-screen-from: 321px !default;
$mobile-screen-to: 479px !default;

/* Extra Small */
$small-tablet-screen-from: 480px !default;
$small-tablet-screen-to: 767px !default;

/* Small */
$tablet-screen-from: 768px !default;
$tablet-screen-to: 991px !default;

/* Medium */
$small-desktop-screen-from: 992px !default;
$small-desktop-screen-to: 1199px !default;

/* Large */
$desktop-screen-from: 1200px !default;
$desktop-screen-to: 1439px !default;

/* Extra Large */
$large-desktop-screen-from: 1440px !default;
